import React from 'react';
import { useAuth } from '../../contexts/AuthContext';
import Logout from '../Logout';

const Navbar = () => {
    const { user } = useAuth();

    return (
        <nav>
            <a href="/"></a>
            <a href="/guide"></a>
            {user ? (
                <>
                    {/* ,    */}
                    <a href="/profile"></a>
                    <Logout />
                </>
            ) : (
                <>
                    {/* ,     */}
                    <a href="/login"></a>
                    <a href="/signup"></a>
                </>
            )}
        </nav>
    );
};

export default Navbar;