import React from 'react';
import { auth } from '../../firebase';

const Logout = () => {
    const handleLogout = async () => {
        try {
            await auth.signOut();
            // alert('    .');
        } catch (error) {
            console.log(`    : ${error.message}`);
        }
    };

    return (
        <button onClick={handleLogout}></button>
    );
};

export default Logout;